.btnLang{
    color: #FFFFFF;
    display: inline-block;
    padding: 0;
    position: relative;
    text-align: center;
    transition: background 600ms ease, color 600ms ease;
    font-size: 1.5vw;
    font-family: 'formulaCondensed';
    width: 2vw;
    height: 2vw;
    top: 3%;
    right: 3%;
  }
  
  input[type="radio"].toggle {
    display: none;
    & + label{
        cursor: pointer;
        min-width: 2vw;
        &:hover{
            background: none;
            color: #FFFFFF;
        }
        &:after{
            background: #FFFFFF;
            content: "";
            height: 100%;
            position: absolute;
            top: 0;
            transition: left 200ms cubic-bezier(0.77, 0, 0.175, 1);
            width: 100%;
            z-index: -1;
        }
    }
    &.toggle-left + label {
        border-right: 0;
        &:after{
            left: 100%
        }
    }
    &.toggle-right + label{
        margin-left: -5px;
        &:after{
            left: -100%;
        }
    }
    &:checked + label {
        cursor: default;
        color: #f3702100;
        transition: color 200ms;
        &:after{
            left: 0;
        }
    }
  }
  

  #langButton {
    right:10%;
    top:2.9%;
    position: absolute;
  }



