.arrows{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  padding-top: 100%;
  height: auto;
}
.arrows span{
  display: block;
  width: 1.5vw;
  height: 1.5vw;
  border-bottom: 0.2vw solid #f37021;
  border-right: 0.2vw solid #f37021;
  transform: rotate(45deg);
  margin: -0.6vw;
  -webkit-animation: arrowsAnimate 2s infinite;
          animation: arrowsAnimate 2s infinite;
}
.arrows span:nth-child(2){
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
}
.arrows span:nth-child(3){
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}
@-webkit-keyframes arrowsAnimate {
  0%{
      opacity: 0;
      transform: rotate(45deg) translate(-1vw,-1vw);
  }
  50%{
      opacity: 1;
  }
  100%{
      opacity: 0;
      transform: rotate(45deg) translate(1vw,1vw);
  }
}
@keyframes arrowsAnimate {
  0%{
      opacity: 0;
      transform: rotate(45deg) translate(-1vw,-1vw);
  }
  50%{
      opacity: 1;
  }
  100%{
      opacity: 0;
      transform: rotate(45deg) translate(1vw,1vw);
  }
}




/* Arrow */

.arrow {
	position: relative;
  margin: 0 auto;
  width: 100px;
}

.arrow .curve {
	border: 2px solid #f37021;
	border-color: transparent transparent transparent #f37021;
	height: 360px;
	width: 1200px;
	border-radius: 230px 0 0 150px;
}

.arrow .point {
	position: absolute;
	left: 40px;
	top: 315px;
}

.arrow .point:before, .arrow .point:after {
	border: 1px solid #f37021;
	height: 25px;
	content: "";
	position: absolute;
}

.arrow .point:before {
	top: -11px;
	left: -11px;
	transform:rotate(-74deg);
	-webkit-transform:rotate(-74deg);
  -moz-transform:rotate(-74deg);
  -ms-transform: rotate(-74deg);
}

.arrow .point:after {
  top: -20px;
	left: 5px;
	transform:rotate(12deg);
	-webkit-transform: rotate(12deg);
  -moz-transform:rotate(12deg);
  -ms-transform: rotate(12deg);
}

